



















export default {
	data() {
		return {
			showPassword: true,
			vertyText: "获取验证码",
			currentTime: 60,
			disabled: false,
			vertrycode:'',
			pass:''
		}
	},
	methods: {
		getCode: function() {
			var that = this;
			this.$utils.tRequest({
				url: this.$webUrl + "user_verify_smscode",
				data: {
					mobile: this.phone
				},
				success: res => {
					that.AppSendVerifyCode(res);
				},
				fail: () => {
			
				},
				complete: () => {
			
				}
			})
		},
		AppSendVerifyCode(res) {
			
			var that = this;
			if (res.code == 0) {
				var currentTime = that.currentTime;
				this.interval = setInterval(function() {
					that.disabled = true
					currentTime--;
		
					that.vertyText = currentTime + '秒'
					if (currentTime <= 0) {
						clearInterval(that.interval)
						that.vertyText = '重新发送',
							that.currentTime = 60,
							that.disabled = false
					}
		
				}, 1000)
			} else {
				uni.showModal({
					title: '提示',
					content: res.msg,
					showCancel: false,
					cancelText: '',
					confirmText: '知道了',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		changePassword: function() {
			this.showPassword = !this.showPassword;
		},
		update:function(){
			
			this.$utils.tRequest({
				url: this.$webUrl+'user_paypassword_edit',
				method: 'POST',
				data: {
					code:this.vertrycode ,
					password:this.pass
				},
				success: res => {
					if(res.code == 0){
						this.$store.dispatch("setPayPass",this.pass)
					}
					uni.showToast({
						title: res.msg,
						mask: false,
						duration: 1500,
						icon:'none'
					});
				},
				fail: () => {},
				complete: () => {}
			})
		}
	},
	computed: {
		phone(){
			return this.$store.getters.getUserInfo.mobile
		}
	}

}
